.cardList {
  width: 80vw;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card {
  border: 1px solid grey;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: aqua;
  border-radius: 5px;
  text-align: center;
}

h1 {
  font-family: 'Bigelow Rules';
  font-size: 4rem;
  color: aqua;
}

.top {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 margin: 1rem 4rem 1rem 4rem;
}
.App {
  background-color: black;
}